.a {
  position: relative;
  z-index: 1;
}

.a::before {
  content: '';
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: -0.25em;
  right: -0.25em;
  background-color: rgba(60,239,255, 0.42);
  transform-origin: center right;
  transform: scaleX(0);
  transition: transform 0.2s ease-in-out;
}

.a:hover::before {
  transform: scaleX(1);
  transform-origin: center left;
}
